/* General Page Styling */
body {
  font-family: "Georgia", serif;
  line-height: 1.7;
  background-color: #fafafa;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Modern Blog-Style Header (Compact Version) */
.about-header {
  background: #fafafa;
  color: #111827;
  text-align: center;
  padding: 80px 20px 60px; /* ⬅️ reduced height */
  border-bottom: 1px solid #e5e7eb;
}

.about-header .framed-text {
  max-width: 760px; /* slightly narrower for focus */
  margin: 0 auto;
}

/* Title */
.about-header h1 {
  font-family: 'Libre Baskerville', Georgia, serif;
  font-size: clamp(2.2rem, 4.5vw, 3rem);
  font-weight: 700;
  color: #111827;
  line-height: 1.2;
  letter-spacing: -0.015em;
  margin-bottom: 14px; /* tighter spacing */
}

/* Subtitle */
.about-header p {
  font-family: 'Inter', sans-serif;
  font-size: 1.1rem;
  color: #4b5563;
  line-height: 1.65;
  max-width: 640px;
  margin: 0 auto 28px; /* slightly less bottom space */
}

/* Meta (author/date) */
.about-meta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
  color: #6b7280;
}

.about-meta span {
  display: flex;
  align-items: center;
  gap: 4px;
}

.about-meta .dot {
  font-size: 1.1rem;
  color: #9ca3af;
}

/* Blog Content */
.post-content {
  max-width: 800px;
  margin: 60px auto;
  padding: 0 20px;
  background: white;
  border-radius: 8px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
}

.post-content p {
  font-size: 1.05rem;
  margin: 20px 0;
  text-align: justify;
}

/* Images */
.post-image-container {
  margin: 30px 0;
  text-align: center;
}

.post-image {
  max-width: 100%;
  height: auto;
  border-radius: 6px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}

/* Links */
.post-content a {
  color: #357ab8;
  text-decoration: none;
  border-bottom: 1px solid #357ab8;
  transition: 0.2s ease;
}

.post-content a:hover {
  color: #000000;
  border-bottom-color: #000000;
}

/* Special Notes (like side tangents) */
.post-content p::first-letter {
  font-size: 1.4rem;
  font-weight: bold;
  color: #202020; /* changed from blue to black */
}

/* Responsive */
@media (max-width: 768px) {
  .about-header h1 {
    font-size: 2.2rem;
  }

  .about-header p {
    font-size: 1rem;
  }

  .post-content {
    margin: 40px 10px;
    padding: 15px;
  }
}
